@import "@/assets/scss/all.scss";










































































































































#Home {
  padding: 0 8%;
  padding-top: 20px;
  padding-bottom: 30px;
  // min-height: 100vh;
  h2 {
    font-size: 48px;
  }
  .MyImag {
    img {
      display: block;
      width: 500px;
      min-height: auto;
      margin: 0 auto;
      @include dai_425 {
        width: 100%;
      }
    }
  }
  .MyInt {
    padding-left: 20px;
    @include dai_425 {
      padding-left: 10px;
    }
    h3 {
      border-left: 2px solid #000;
      border-bottom: 2px solid #000;
      padding: 0 0 10px 5px;
      width: 20%;
      font-weight: 900;
      @include dai_425 {
        width: 70%;
      }
    }
    p {
      font-size: 23px;
      padding-left: 20px;
      font-weight: 500;
      @include dai_425 {
        line-height: 1.5;
        padding-left: 0;
      }
    }
  }
}
#Gallery {
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  padding-top: 58px;
  h2 {
    font-size: 48px;
    color: white;
  }
  .GalleryList {
    padding: 0 8%;
    ul {
      @include flex_pos(flex-start, center);
      flex-wrap: wrap;
      li {
        width: calc(100% / 4 - 40px);
        margin: 0 20px;
        margin-bottom: 50px;
        background-color: rgba(0, 0, 0, 0);
        min-height: 550px;
        @include flex_pos(flex-start, center, column);
        p {
          width: 100%;
          color: #fff;
          font-size: 16px;
          margin-bottom: 10px;
          line-height: 20px;
        }
        h3 {
          color: #fff;
          font-size: 24px;
          font-weight: bold;
          margin-bottom: 10px;
        }
        img {
          display: block;
          width: 100%;
          height: auto;
        }
      }
    }
  }
}
