@import "@/assets/scss/all.scss";















































.LoginPicture {
  position: relative;
  height: 100vh;
  background-image: radial-gradient(rgba(0, 0, 0, 0.05), black),
    url(../assets/images/switzerland-4290226_1920.jpg);
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  z-index: 1;
  p {
    font-size: 60px;
    color: white;
    @include dai_425 {
      font-size: 35px;
    }
  }
  .fas {
    font-size: 40px;
    @include dai_425 {
      font-size: 70px;
    }
  }
  .Community {
    a {
      color: lighten($color: #000000, $amount: 20%);
      transition: color 0.5s;
      &:hover {
        color: lighten($color: #000000, $amount: 0%);
      }
    }
    .fab {
      font-size: 40px;
    }
  }
  .Anchor {
    color: white;
    position: absolute;
    bottom: 80px;
    right: 50%;
    max-width: 50px;
    margin-right: -25px;
    animation: fontbulger 1.5s infinite;
    &:hover {
      cursor: pointer;
    }
  }
}
@keyframes fontbulger {
  0% {
    bottom: 50px;
  }
  30% {
    bottom: 100px;
  }
  100% {
    bottom: 50px;
  }
}
