@import "@/assets/scss/all.scss";































































































































































.wrae {
  max-width: 1024px;
  margin: 0 auto;
  .header-img {
    min-height: 380px;
    max-width: 100%;
    background-image: url(https://i.imgur.com/i4GvwRh.png);
    background-size: cover;
    background-position: center center;
    .header-title {
      padding-top: 50px;
      h1 {
        font-family: PingFangTC-Regular;
        font-size: 40px;
        color: #ffffff;
        letter-spacing: 3.89px;
      }
    }
    .manu {
      margin-top: 20px;
      position: relative;
      text-align: center;
      .area {
        margin-top: 80px;
        max-width: 452px;
        height: 30px;
        background: rgba(255, 255, 255, 0.7);
        border: 3px solid #ffffff;
        border-radius: 3px;
        text-align: center;
      }
    }
  }
}

.fire-top {
  ul {
    max-width: 800px;
    min-height: 112px;
    background: #ffffff;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    margin: 0 auto;
    margin-top: -50px;
    h3 {
      font-family: AppleSystemUIFont;
      font-size: 18px;
      color: #000000;
      padding: 20px 0 0 20px;
    }
    li {
      text-align: center;
      width: 130px;
      height: 42px;
      margin-left: 90px;
      margin-top: 10px;
      @include mouse;
      @include TextCenter;
      @include dai_425 {
        margin-top: 20px;
        margin-left: 0px;
      }
    }
  }
}

.content {
  margin: auto;
  width: 100%;
  height: 100%;
  .content-title {
    font-family: SFNSDisplay;
    font-size: 36px;
    color: #8a82cc;
    text-align: center;
  }
  .content-ul {
    max-width: 100%;
    min-height: auto;
    text-overflow: ellipsis;
    .content-li {
      background: #ffffff;
      box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
      float: left;
      width: 46%;
      margin-left: 2%;
      margin-right: 2%;
      box-sizing: border-box;
      margin-top: 20px;
      border-radius: 10px;
      @include dai_425 {
        float: none;
        width: 96%;
      }
      &:hover {
        box-shadow: 5px 5px 60px rgba(0, 0, 0, 0.4);
        transform: translate(-5px, -5px);
      }
      .land-img {
        max-width: 500px;
        min-height: 155px;
        background-size: cover;
        background-position: center center;
        position: relative;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        .land-text {
          width: 100%;
          background-image: linear-gradient(
            -180deg,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.54) 100%
          );
          h3 {
            font-family: SFNSDisplay;
            font-size: 24px;
            color: #ffffff;
            background-color: rgba(0, 0, 0, 0.8);
            border-radius: 5px;
            position: absolute;
            left: 10px;
            bottom: 0;
          }
          h5 {
            font-family: PingFangTC-Regular;
            font-size: 16px;
            color: #ffffff;
            position: absolute;
            background-color: rgba(0, 0, 0, 0.8);
            border-radius: 5px;
            bottom: 0;
            right: 10px;
          }
        }
      }
      .land-info {
        li {
          padding: 10px 10px 10px 10px;
          // &:hover {
          //   box-shadow: 5px 5px 60px rgba(0, 0, 0, 0.4);
          //   transform: translate(-5px, -5px);
          // }
        }
      }
    }
  }
}
.footer {
  max-width: 100%;
  height: 90px;
  margin: 0 auto;
  background-color: #559ac8;
  clear: both;
  text-align: center;
  font-family: AppleSystemUIFont;
  p {
    font-size: 14px;
    color: #ffffff;
    padding-top: 24px;
  }
  a {
    font-size: 14px;
    color: #ffd366;
  }
}
#map {
  height: 400px;
  max-width: 600px;
  margin-top: 20px;
  border-radius: 10px;
}
