@import "@/assets/scss/all.scss";









































































































#app {
  position: relative;
}
#nav {
  button.navbar-toggler {
    color: rgba(255, 255, 255, 0.6);
    border-color: rgba(255, 255, 255, 0.4);
  }
  .navbar-nav {
    align-items: center;
    justify-content: center;
    .nav-item {
      .nav-link {
        color: white;
        font-size: 20px;
      }
      &:nth-of-type(1) {
        .nav-link {
          font-size: 26px;
          margin-right: 20px;
          @include dai_425 {
            margin: 0;
          }
        }
      }
    }
  }
}
